  onClick_Invest = () => {
    if(!this.amountInput.current || !this.sponsorInput.current){
      return;
    }

    
      let ref = window.refferalAddress;
      if (!window.web3.utils.isAddress(ref)){
        ref=this.sponsorInput.current.value as string;
        //console.log("this.state.myUpline", this.state.myUpline)
        if(this.state.myUpline == "" || this.state.myUpline == "0x0000000000000000000000000000000000000000"){
          if(ref.length > 0 && !window.web3.utils.isAddress(ref) ){
            // no address, but value present --> check if its a valid nickname
            window.farm_contract.methods.getAddressToNickname(ref).call().then((result: string) => {
              console.log("checked nickname ", ref, " and got address ", result);
              if( result != "0x0000000000000000000000000000000000000000"){

                // invest with nickname
                if(!this.amountInput.current){
                  return;
                }
                console.debug("invest with nickname ", ref);

                window.farm_contract.methods.depositWithNickname( ref )
                  .send({ value: window.web3.utils.toWei(this.amountInput.current.value as string), from:window.walletAddress}).then((response:any) => {
              
                  }).catch((err:any) => {
                    console.log(err)
                  }
                );

              }else{
                alert("Invalid Sponsor Address");
                return;
              }
            });  
            
            return; // exit here. async forward
          }else if(ref.length == 0 ){
            if(window.confirm("You cannot join without a sponsor. would you like to use the project's default sponsor to join?") == true){

                this.setState({sponsorAddress: this.defaultSponsorAddress});

            }else{
              return;
            }
          }else{
            if(!window.web3.utils.isAddress(ref) || ref == window.walletAddress){
              alert("Sponsor Addres not valid");
              return;
            }
          }
        }else{
          ref = this.state.myUpline; // wenn bereits upline vorhanden ist, dann immer diese verwenden. wird dann bei setUpline im contract sowieso ignoriert
        }
      }
      if(ref.length == 0){
        //ref = this.state.projectWallet;
        ref = this.defaultSponsorAddress;
        //console.log("Invest with Project Wallet as Sponsor.");
      }    

      // invest with upline address
      if(!this.amountInput.current){
        return;
      }

      console.debug("invest with address ", ref);
      window.farm_contract.methods.deposit( ref )
        .send({ value: window.web3.utils.toWei(this.amountInput.current.value as string), from:window.walletAddress}).then((response:any) => {
    
        }).catch((err:any) => {
          console.log(err)
        }
      );
  }

  onClick_Reinvest = () => {
    window.farm_contract.methods.reinvest().send({from: window.walletAddress}).then((result: TransactionReceipt) => {
      alert("Re-Invest Transaction Submitted.")
    }).catch((err:any) => {
      console.log(err)
    });
  }

  onClick_Withdraw = () => {
    window.farm_contract.methods.withdraw().send({from: window.walletAddress}).then((result: TransactionReceipt) => {
      alert("Withdraw Transaction Submitted.")
    }).catch((err:any) => {
      console.log(err)
    });
  }



